const EmbeddedUserManual = ({ documents }) => {
  const [selectedDocument, setSelectedDocument] = useState(documents?.[0]?.embedUrl || null);

  if (!documents || documents.length === 0) {
    return <p>No documents available for this department.</p>;
  }
  console.log(documents, selectedDocument);
  return (
    <div>
      {/* Document List */}
      <h3>Select a Document</h3>
      <ul>
        {documents.map((doc, index) => (
          <li key={index}>
            <button
              onClick={() => setSelectedDocument(doc.embedUrl)}
              style={{
                padding: "10px 20px",
                backgroundColor: "#0078d7",
                color: "#ffffff",
                textDecoration: "none",
                borderRadius: "5px",
                marginTop: "10px",
                cursor: "pointer",
              }}>
              {doc.title}
            </button>
          </li>
        ))}
      </ul>

      {/* Embed the Selected Document */}
      {selectedDocument ? (
        <iframe
          src={selectedDocument}
          width='100%'
          height='600px'
          frameBorder='0'
          style={{ border: "none" }}
          allowFullScreen
          title='Document Viewer'>
          This is an embedded Microsoft Office document, powered by Office.
        </iframe>
      ) : (
        <p>No document selected</p>
      )}
    </div>
  );
};





