import React, {useState} from "react";

function FAQ() {
  const [questions, setQuestions] = useState([]);
  const [filter, setFilter] = useState("all"); // 'all', 'answered', 'unanswered'
  const [searchQuery, setSearchQuery] = useState("");
  const [newQuestion, setNewQuestion] = useState("");

  const handlePostQuestion = (e) => {
    e.preventDefault();
    if (newQuestion.trim() !== "") {
      const newQuestionObj = {
        id: Date.now(),
        question: newQuestion,
        answer: "",
        author: "Anonymous",
        date: new Date().toISOString(),
        status: "unanswered",
      };
      setQuestions([...questions, newQuestionObj]);
      setNewQuestion("");
    }
  };

  const filteredQuestions = questions.filter((q) => {
    const matchesFilter = filter === "all" || q.status === filter;
    const matchesSearch = q.question.toLowerCase().includes(searchQuery.toLowerCase());
    return matchesFilter && matchesSearch;
  });

  return (
    <div>
      <h2>Frequently Asked Questions</h2>
      <form onSubmit={handlePostQuestion} style={{marginBottom: "20px"}}>
        <textarea
          value={newQuestion}
          onChange={(e) => setNewQuestion(e.target.value)}
          placeholder='Type your question here...'
          required
          style={{width: "100%", height: "80px", padding: "10px"}}></textarea>
        <button
          type='submit'
          style={{
            marginTop: "10px",
            padding: "10px 20px",
            backgroundColor: "#1e272e",
            color: "#ffffff",
            border: "none",
            borderRadius: "5px",
            cursor: "pointer",
          }}>
          Post Question
        </button>
      </form>

      <div style={{marginBottom: "20px"}}>
        <input
          type='text'
          placeholder='Search questions...'
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          style={{padding: "10px", width: "100%", marginBottom: "10px"}}
        />
        <div>
          <button
            onClick={() => setFilter("all")}
            disabled={filter === "all"}
            style={getFilterButtonStyle(filter === "all")}>
            All
          </button>
          <button
            onClick={() => setFilter("answered")}
            disabled={filter === "answered"}
            style={getFilterButtonStyle(filter === "answered")}>
            Answered
          </button>
          <button
            onClick={() => setFilter("unanswered")}
            disabled={filter === "unanswered"}
            style={getFilterButtonStyle(filter === "unanswered")}>
            Unanswered
          </button>
        </div>
      </div>

      <ul style={{listStyleType: "none", padding: 0}}>
        {filteredQuestions.length > 0 ? (
          filteredQuestions.map((q) => (
            <li
              key={q.id}
              style={{
                backgroundColor: "#ffffff",
                padding: "15px",
                marginBottom: "10px",
                borderRadius: "5px",
              }}>
              <h4 style={{margin: "0 0 10px"}}>{q.question}</h4>
              {q.answer ? (
                <p>
                  <strong>Answer:</strong> {q.answer}
                </p>
              ) : (
                <p style={{fontStyle: "italic"}}>This question has not been answered yet.</p>
              )}
              <p style={{fontSize: "0.9em", color: "#555"}}>
                Posted by {q.author} on {new Date(q.date).toLocaleDateString()}
              </p>
            </li>
          ))
        ) : (
          <p>No questions found.</p>
        )}
      </ul>
    </div>
  );
}

function getFilterButtonStyle(isActive) {
  return {
    padding: "10px 15px",
    marginRight: "10px",
    backgroundColor: isActive ? "#485460" : "#1e272e",
    color: "#ffffff",
    border: "none",
    borderRadius: "5px",
    cursor: isActive ? "default" : "pointer",
  };
}

export default FAQ;
